import axios from "../utils/axios";

// export function addAddress(data) {
//   return axios.post("/ctsi/address", data);
// }
export function addAddress(data) {
  return axios({
    url: "/ctsi/address",
    method: "post",
    data: data,
    headers: {
      isRepeatSubmit: true,
    },
  });
}
