export function getAddressList() {
  return axios.get("/ctsi/address/list");
}

// export function getAddressDetail(id) {
//   return axios.get(`/address/${id}`);
// }
export function getProvice() {
  return axios.get("/ctsi/address/province/list");
}
export function getCity(code) {
  return axios.get(`/ctsi/address/city/list?provinceCode=${code}`);
}
export function getDistrict(code) {
  return axios.get(`/ctsi/address/district/list?cityCode=${code}`);
}

export function deleteAddress(id) {
  return axios.delete(`/ctsi/address/${id}`);
}
