import axios from "axios";
import { Toast } from "vant";
import { setLocal, getLocal } from "@/common/js/utils";
import router from "../router";
import cache from "./cache";

const errorCode = {
  401: "认证失败，无法访问",
  403: "当前操作没有权限",
  404: "访问资源不存在",
  default: "系统未知错误",
};
axios.defaults.headers["Content-Type"] = "application/json;charset=utf-8";
// 创建axios实例
const service = axios.create({
  // axios中请求配置有baseURL选项，表示请求URL公共部分
  baseURL: "https://xbk.189.cn/rich-media-api",
  // baseURL: "http://172.16.10.20:8188",
  // 超时
  timeout: 10000,
});

// request拦截器
service.interceptors.request.use(
  (config) => {
    // 是否需要设置 token
    const isToken = (config.headers || {}).isToken === false;
    // 是否需要防止数据重复提交
    const isRepeatSubmit = (config.headers || {}).repeatSubmit === false;
    if (getLocal("token") && !isToken) {
      config.headers["Authorization"] = "Bearer " + getLocal("token"); // 让每个请求携带自定义token 请根据实际情况自行修改
    }
    // // get请求映射params参数
    if (config.method === "get" && config.params) {
      let url = config.url + "?";
      for (const propName of Object.keys(config.params)) {
        const value = config.params[propName];
        var part = encodeURIComponent(propName) + "=";
        if (value !== null && typeof value !== "undefined") {
          if (typeof value === "object") {
            for (const key of Object.keys(value)) {
              let params = propName + "[" + key + "]";
              var subPart = encodeURIComponent(params) + "=";
              url += subPart + encodeURIComponent(value[key]) + "&";
            }
          } else {
            url += part + encodeURIComponent(value) + "&";
          }
        }
      }
      url = url.slice(0, -1);
      config.params = {};
      config.url = url;
    }
    // if (config.method === "get" && config.params) {
    //   let url = config.url + "?" + tansParams(config.params);
    //   url = url.slice(0, -1);
    //   config.params = {};
    //   config.url = url;
    // }
    if (!isRepeatSubmit && config.method === "post") {
      const requestObj = {
        url: config.url,
        data:
          typeof config.data === "object"
            ? JSON.stringify(config.data)
            : config.data,
        time: new Date().getTime(),
      };
      const sessionObj = cache.session.getJSON("sessionObj");
      if (
        sessionObj === undefined ||
        sessionObj === null ||
        sessionObj === ""
      ) {
        cache.session.setJSON("sessionObj", requestObj);
      } else {
        const s_url = sessionObj.url; // 请求地址
        const s_data = sessionObj.data; // 请求数据
        const s_time = sessionObj.time; // 请求时间
        const interval = 1000; // 间隔时间(ms)，小于此时间视为重复提交
        if (
          s_data === requestObj.data &&
          requestObj.time - s_time < interval &&
          s_url === requestObj.url
        ) {
          Toast({
            message: "正在处理，请勿重复提交",
            type: "fail",
          });
          return Promise.reject(new Error("正在处理，请勿重复提交"));
        } else {
          cache.session.setJSON("sessionObj", requestObj);
        }
      }
    }
    return config;
  },
  (error) => {
    console.log(error);
    Promise.reject(error);
  }
);
// 响应拦截器
service.interceptors.response.use(
  (res) => {
    // 未设置状态码则默认成功状态
    const code = res.data.code || 200;
    // 获取错误信息
    const msg = errorCode[code] || res.data.msg || errorCode["default"];
    // // 二进制数据则直接返回
    // if(res.request.responseType ===  'blob' || res.request.responseType ===  'arraybuffer'){
    //   return res.data
    // }
    if (code === 401) {
      //   if (!isRelogin.show) {
      //     isRelogin.show = true;
      //     MessageBox.confirm('登录状态已过期，您可以继续留在该页面，或者重新登录', '系统提示', {
      //       confirmButtonText: '重新登录',
      //       cancelButtonText: '取消',
      //       type: 'warning'
      //     }
      //   ).then(() => {
      //     isRelogin.show = false;
      //     store.dispatch('LogOut').then(() => {
      //       location.href = '/index';
      //     })
      //   }).catch(() => {
      //     isRelogin.show = false;
      //   });
      // }
      Toast({
        message: msg,
        type: "fail",
      });
      window.location.href = "#/h5Login";
      // window.location.href = "https://xbk.189.cn/baidu-h5/#/h5Login";
      //window.location.href = "https://xbk.189.cn/baidu-h5-numberCard/#/h5Login";
      return Promise.reject(new Error(msg));
    } else if (code === 400) {
      // 号码校验
      Toast({
        message: msg,
        type: "fail",
      });
      return Promise.reject(new Error(msg));
    } else if (code === 500) {
      if (
        msg.indexOf("用户") !== -1 ||
        msg.indexOf("密码") !== -1 ||
        msg.indexOf("手机") !== -1
      ) {
        Toast({
          message: msg,
          type: "fail",
        });
        // window.location.href = "#/h5Login";
        // window.location.href = "https://xbk.189.cn/baidu-h5/#/h5Login";
        window.location.href =
          "https://xbk.189.cn/baidu-h5-numberCard/#/h5Login";
        return Promise.reject(new Error(msg));
      } else {
        Toast({
          message: "请您稍后再试",
          type: "fail",
        });
        return Promise.reject(new Error(msg));
      }
    } else if (code !== 200) {
      Toast({
        message: "请您稍后再试",
        type: "fail",
      });
      return Promise.reject(new Error(msg));
    } else {
      return res.data;
    }
  },
  (error) => {
    console.log("err" + error);
    // let { message } = error;
    // if (message == "Network Error") {
    //   message = "后端接口连接异常";
    // } else if (message.includes("timeout")) {
    //   message = "系统接口请求超时";
    // } else if (message.includes("Request failed with status code")) {
    //   message = "系统接口" + message.substr(message.length - 3) + "异常";
    // }
    // Message({
    //   message: message,
    //   type: "error",
    //   duration: 5 * 1000,
    // });
    return Promise.reject(error);
  }
);
export default service;
