import { getCart } from "../service/cart";
// import { getAddressList } from "../mock/api";
import { getAddressList } from "../service/address";

export default {
  async initAddress(ctx) {
    const { total, rows } = await getAddressList();
    if (total) {
      ctx.commit("addAddress", {
        address: { ...rows[0] },
      });
    } else {
      const BMapGL = window.BMapGL;
      var geolocation = new BMapGL.Geolocation();
      // 开启SDK辅助定位
      geolocation.enableSDKLocation();
      geolocation.getCurrentPosition(function (r) {
        // eslint-disable-next-line no-undef
        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
          // console.log(r);
          const address = {
            ...r.address,
          };
          address.latitude = r.latitude;
          address.longitude = r.longitude;
          // console.log(address);
          ctx.commit("addAddress", { address });
        } else {
          console.log("failed" + this.getStatus());
        }
      });
    }
  },
  updateAddress(ctx, address) {
    ctx.commit("addAddress", { address });
  },
  deleteAddress(ctx) {
    ctx.commit("deleteAddress");
  },
  updateHall(ctx, hall) {
    ctx.commit("addHall", { hall });
  },
};
